/* eslint-enable */
const MAX_LINK_DENSITY_DEFAULT = 0.2;
const LENGTH_LOW_DEFAULT = 70;
const LENGTH_HIGH_DEFAULT = 200;
const STOPWORDS_LOW_DEFAULT = 0.30;
const STOPWORDS_HIGH_DEFAULT = 0.32;
const NO_HEADINGS_DEFAULT = false;
const MAX_HEADING_DISTANCE_DEFAULT = 200;

export function stoplistBy(language) {
  logger.debug('language', language);
  const isExist = STOP_LISTS_JSON.filter(item => item.name === language);
  let result = [];
  if (isExist && isExist[0] && isExist[0].data) {
    result = new S(isExist[0].data).lines();
  }
  logger.debug('stoplistBy', result);
  return result;
}

export function rawHtml(htmlText, language = '', format = 'default', options = {
  lengthLow: LENGTH_LOW_DEFAULT,
  lengthHigh: LENGTH_HIGH_DEFAULT,
  stopwordsLow: STOPWORDS_LOW_DEFAULT,
  stopwordsHigh: STOPWORDS_HIGH_DEFAULT,
  maxLinkDensity: MAX_LINK_DENSITY_DEFAULT,
  maxHeadingDistance: MAX_HEADING_DISTANCE_DEFAULT,
  noHeadings: NO_HEADINGS_DEFAULT,
}) {
  const core = new Core();
  let stopwordsLow = options.stopwordsLow;
  let stopwordsHigh = options.stopwordsHigh;
  let stoplist = [];
  // read stoplist file by language
  if (language.length > 0) {
    stoplist = stoplistBy(language);
  } else {
    // empty stoplist, switch to language-independent mode
    logger.warn('No stoplist specified.');
    stopwordsHigh = 0;
    stopwordsLow = 0;
  }

  const paragrahps = core.jusText(htmlText, stoplist, options.lengthLow, options.lengthHigh,
    stopwordsLow, stopwordsHigh, options.maxLinkDensity,
    options.maxHeadingDistance, options.noHeadings);
  switch (format) {
    case 'default':
      return Presenter.defaultOuptut(paragrahps);
    case 'boilerplate':
      return Presenter.defaultOuptut(paragrahps, false);
    case 'detailed':
      return Presenter.detailOuptut(paragrahps, stoplist);
    case 'krdwrd':
      return Presenter.krdwrdOuptut(paragrahps);
    default:
      throw new Error('Unknown format');
  }
}


export function url(externalUrl, language = '', format = 'default', options = {
  lengthLow: LENGTH_LOW_DEFAULT,
  lengthHigh: LENGTH_HIGH_DEFAULT,
  stopwordsLow: STOPWORDS_LOW_DEFAULT,
  stopwordsHigh: STOPWORDS_HIGH_DEFAULT,
  maxLinkDensity: MAX_LINK_DENSITY_DEFAULT,
  maxHeadingDistance: MAX_HEADING_DISTANCE_DEFAULT,
  noHeadings: NO_HEADINGS_DEFAULT,
}) {
  Core.getHtmlOfUrl(externalUrl).then((response) => {
    const htmlText = response.data;
    return rawHtml(htmlText, language, format, options);
  }).catch((error) => {
    throw error;
  });
}

export function decode(text) {
  const htmlDecoding = new entities.AllHtmlEntities();
  return htmlDecoding.decode(text);
}



